                    @click="addToCart(product.id)"
                  >
                    <i class="icofont-basket"></i>
                    <span>افزودن به سبد خرید</span>
                    <i class="fas fa-spinner fa-spin d-none spinner"></i>
                    <i class="fas fa-check d-none check"></i>
                  </a>
                </div>
                <div v-else class="p-2">قبلا خریداری شده است</div>
              </div>
            </div>
          </div>
        </div>

        <div class="row store-item my-3">
          <div class="col-md-3 mb-3 mb-md-0">
            <img
              class="w-100 rounded-1"
              :src="API_STORAGE_URL + product.main_image_path"
              @error="noPic"
            />
          </div>

          <div class="col-md-9">
            <div class="card mb-4">
              <div class="card-header fw-bold fs-4">توضیحات</div>
              <div class="card-body">
                <div class="card-text">
                  <div>
                    <p v-html="product.short_description" class="fs-4"></p>
                    <p class="mb-3 text-muted"></p>

                    <a
                      class="
                        text-default
                        d-block
                        text-center text-decoration-none
                        fs-6
                      "
                      role="button"
                      @click="showDescription = !showDescription"
                    >
                      توضیحات بیشتر
                      <i class="fas fa-chevron-down"></i>
                    </a>
                  </div>
                  <div
                    :class="showDescription ? '' : 'collapse'"
                    id="collapse-link"
                    style=""
                  >
                    <p v-html="product.long_description" class="fs-5"></p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="row my-3">
            <div class="col-lg-4 col-xl-3 text-center mb-5 mb-lg-0">
              <div class="card">
                <table class="table fs-6">
                  <tbody>
                    <tr>
                      <td>
                        <i class="icofont-ui-calendar"></i>روزهای برگزاری:
                      </td>
                      <td>{{ product.days }}</td>
                    </tr>
                    <tr>
                      <td><i class="icofont-clock-time"></i>ساعت برگزاری:</td>
                      <td>{{ product.hour }}</td>
                    </tr>

                    <tr>
                      <td><i class="icofont-ui-calendar"></i>تاریخ شروع:</td>
                      <td>{{ product.start_date }}</td>
                    </tr>
                    <tr>
                      <td><i class="icofont-hat"></i>نظام تحصیلی:</td>
                      <td>{{ product.education_system }}</td>
                    </tr>
                    <tr>
                      <td><i class="icofont-price"></i>قیمت:</td>
                      <td class="price">
                        <div v-if="product.sale_price == product.price">
                          <span class="fw-bold">
                            {{ this.utils.monize(product.price) }}
                            تومان
                          </span>
                          <br />
                          <span>&nbsp;</span>
                        </div>
                        <div v-else>
                          <span
                            class="
                              text-danger text-decoration-line-through
                              fw-bold
                            "
                          >
                            {{ this.utils.monize(product.price) }}
                          </span>
                          <br />
                          <span class="text-success fw-bold">
                            {{ this.utils.monize(product.sale_price) }}
                            تومان
                          </span>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div v-if="product.type === 'package'">
                  <div v-if="buyed_before == 0">
                    <a
                      class="
                        btn btn-primary
                        ml-3
                        rounded-0 rounded-bottom
                        w-100
                      "
                      @click="addPackageToCart(product.id)"
                    >
                      <i class="fas fa-shopping-cart"></i>
                      <span>افزودن به سبد خرید.</span>
                      <i class="fas fa-spinner fa-spin d-none spinner"></i>
                      <i class="fas fa-check d-none check"></i>
                    </a>
                  </div>
                  <div v-else class="p-2">قبلا خریداری شده است</div>
                </div>
                <div v-else-if="product.type !== 'chairs'">
                  <div v-if="buyed_before == 0">
                    <a
                      class="
                        btn btn-primary
                        ml-3
                        rounded-0 rounded-bottom
                        w-100
                      "
                      @click="addToCart(product.id)"
                    >
                      <i class="fas fa-shopping-cart"></i>
                      <span>افزودن به سبد خرید</span>
                      <i class="fas fa-spinner fa-spin d-none spinner"></i>
                      <i class="fas fa-check d-none check"></i>
                    </a>
                  </div>
                  <div v-else class="p-2">قبلا خریداری شده است</div>
                </div>
              </div>
            </div>

            <div class="col-lg-8 col-xl-9 border-bottom">
              <nav>
                <div class="nav nav-tabs" id="nav-tab" role="tablist">
                  <button
                    class="nav-link active fs-5 w-50"
                    id="nav-home-tab"
                    data-bs-toggle="tab"
                    data-bs-target="#nav-home"
                    type="button"
                    role="tab"
                    aria-controls="nav-home"
                    aria-selected="true"
                  >
                    <template v-if="product.type === 'package'">
                      محصولات زیر مجموعه
                    </template>
                    <template v-else-if="product.type === 'video'">
                      محتویات دوره
                    </template>
                    <template v-else-if="product.type === 'chairs'">
                      فهرست صندلی ها
                    </template>
                  </button>
                  <button
                    class="nav-link fs-5 w-50"
                    id="nav-profile-tab"
                    data-bs-toggle="tab"
                    data-bs-target="#nav-profile"
                    type="button"
                    role="tab"
                    aria-controls="nav-profile"
                    aria-selected="false"
                  >
                    نظرات
                  </button>
                </div>
              </nav>
              <div class="tab-content" id="nav-tabContent">
                <div
                  class="tab-pane fade show active"
                  id="nav-home"
                  role="tabpanel"
                  aria-labelledby="nav-home-tab"
                >
                  <template v-if="product.type === 'video'">
                    <VideoSessions :productId="+productId" />
                  </template>
                  <template v-if="product.type === 'package' && childProducts">
                    <Package
                      :childProducts="childProducts"
                      @setProductChildIds="setProductChildIds"
                    />
                  </template>

                  <template v-if="product.type === 'chairs'">
                    <Chairs :productId="+productId" />
                  </template>
                </div>
                <div
                  class="tab-pane fade"
                  id="nav-profile"
                  role="tabpanel"
                  aria-labelledby="nav-profile-tab"
                >
                  <div v-if="productComments.length > 0">
                    <template v-for="item in productComments" :key="item.id">
                      <div
                        class="
                          border
                          rounded
                          p-2
                          my-1
                          bg-light
                          d-flex
                          justify-content-between
                        "
                      >
                        <div class="fs-6">
                          {{ item.user.first_name ? item.user.first_name : "" }}
                          {{ item.user.last_name }} :
                          <div>
                            {{ item.comment }}
                          </div>
                        </div>
                        <div class="small" dir="ltr">
                          {{
                            jmoment(item.created_at).format(
                              "jYYYY/jMM/jDD HH:mm"
                            )
                          }}
                        </div>
                      </div>
                    </template>
                  </div>
                  <div v-else class="py-2 fs-3">نظری وجود ندارد</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "@/components/TheSidemenu";
import TheTopmenu from "@/components/TheTopmenu";
import ArefApi from "@/libs/api.js";
import utils from "@/libs/utils";
import VideoSessions from "@/components/VideoSessions";
import CartAlert from "../CartAlert";
import jmoment from "moment-jalaali";
import Package from "./components/Package";
import Chairs from "./components/Chairs";

export default {
  name: "Products",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.jmoment = jmoment;
    this.getUserProduct();
    this.getProductComments();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    VideoSessions,
    CartAlert,
    Package,
    Chairs,
  },
  data() {
    return {
      showDescription: false,
      productId: this.$route.params.productId
        ? this.$route.params.productId
        : null,
      buyed_before: this.$route.params.buyed_before
        ? this.$route.params.buyed_before
        : 0,
      product: {
        name: null,
        main_image_path: null,
        short_description: "",
        long_description: "",
      },
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      cartAlert: null,
      childProducts: null,
      productComments: [],
      productChildIds: [],
    };
  },
  methods: {
    getUserProduct() {
      this.arefApi
        .getUserProduct(this.productId)
        .then((resp) => {
          this.product = resp.data;
          if (this.product.short_description) {
            this.product.short_description =
              this.product.short_description.replace(/(?:\r\n|\r|\n)/g, "<br>");
          }
          if (this.product.long_description) {
            this.product.long_description =
              this.product.long_description.replace(/(?:\r\n|\r|\n)/g, "<br>");
          }
          this.getUserChildProducts();
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    addToCart() {
      const products_id = this.productId;
      const obj = {
        products_id: products_id,
      };
      let spinners = document.querySelectorAll(`.spinner`);
      for (let i = 0; i < spinners.length; i++) {
        spinners[i].classList.remove(`d-none`);
      }

      this.arefApi
        .addProductToCart(obj)
        .then((resp) => {
          spinners = document.querySelectorAll(`.spinner`);
          for (let i = 0; i < spinners.length; i++) {
            spinners[i].classList.add(`d-none`);
          }

          let checks = document.querySelectorAll(`.check`);
          for (let i = 0; i < checks.length; i++) {
            checks[i].classList.remove(`d-none`);
          }

          this.$store.commit("set", [
            "cartCount",
            this.utils.getCartCount(resp.data),
          ]);

          this.$store.commit("set", ["cartAmount", resp.data.amount]);
          window.scrollTo(0, top);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getUserChildProducts() {
      if (this.product.type !== "package") {
        return;
      }
      this.arefApi
        .getUserChildProducts(this.productId)
        .then((resp) => {
          this.childProducts = resp.data;
          this.productChildIds = this.childProducts.others.map((x) => x.id);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    noPic(e) {
      e.target.src =
        process.env.VUE_APP_API_STORAGE_URL + process.env.VUE_APP_NO_IMAGE_URL;
    },
    getProductComments() {
      this.arefApi.getUserProductComments(this.productId).then((resp) => {
        this.productComments = resp.data;
      });
    },
    setProductChildIds(productChildIds) {
      this.productChildIds = productChildIds;
    },
    async addPackageToCart() {
      await this.validateChildItems();
      const obj = {
        products_id: this.productId,
        child_product_ids: this.productChildIds,
      };
      let spinners = document.querySelectorAll(`.spinner`);
      for (let i = 0; i < spinners.length; i++) {
        spinners[i].classList.remove(`d-none`);
      }
      this.arefApi
        .addProductPackage(obj)
        .then((resp) => {
          spinners = document.querySelectorAll(`.spinner`);
          for (let i = 0; i < spinners.length; i++) {
            spinners[i].classList.add(`d-none`);
          }

          let checks = document.querySelectorAll(`.check`);
          for (let i = 0; i < checks.length; i++) {
            checks[i].classList.remove(`d-none`);
          }

          this.$store.commit("set", [
            "cartCount",
            this.utils.getCartCount(resp.data),
          ]);

          this.$store.commit("set", ["cartAmount", resp.data.amount]);
          window.scrollTo(0, top);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    validateChildItems() {
      let selectedProductChildIds = Object.values(this.productChildIds);
      if (
        selectedProductChildIds.length === 0 &&
        this.canSecelectProducts.length > 0
      ) {
        this.$swal
          .fire({
            title: "!خطا",
            text: "لطفا یکی از اساتید لیست را انتخاب فرمایید",
            icon: "error",
            confirmButtonText: "متوجه شدم",
          })
          .then(() => {
            setTimeout(() => {
              document.getElementById("selectTeacherDiv").scrollIntoView(true);
            }, 1000);
          });

        return Promise.reject();
      }
      let isExist = false;
      let errorLabel = "";
      for (const i in this.canSecelectProducts) {
        isExist = false;
        errorLabel = i;
        for (const j in this.canSecelectProducts[i]) {
          if (
            selectedProductChildIds.indexOf(this.canSecelectProducts[i][j].id) >
            -1
          ) {
            isExist = true;
          }
        }
        if (!isExist) {
          document.getElementById("selectTeacherDiv").scrollIntoView();
          this.$swal
            .fire({
              title: "!خطا",
              text: `لطفا از آیتم ${errorLabel} یک مورد را علامت بزنید`,
              icon: "error",
              confirmButtonText: "متوجه شدم",
            })
            .then(() => {
              setTimeout(() => {
                document
                  .getElementById("selectTeacherDiv")
                  .scrollIntoView(true);
              }, 1000);
            });

          return Promise.reject();
        }
      }
      return Promise.resolve();
    },
  },
  computed: {
    canSecelectProducts() {
      let out = { ...this.childProducts };
      delete out.others;
      return out;
    },
  },
};
</script>
