<template>
  <div
    v-for="item in videoSessionList"
    :key="item.id"
    class="border-bottom p-4 p-lg-3 bg-white"
  >
    <div class="row align-items-center d-flex row" v-if="item.is_hidden == 0">
      <div
        class="col-xl-2 col-lg-2 col-12 mb-4 mb-lg-0 text-center text-xl-right"
      >
        <img
          class="w-100"
          :src="API_STORAGE_URL + product.thumbnail"
        />
      </div>

      <div class="col-xl-3 col-lg-3 col-12 text-center text-xl-right">
        <div class="fs-5">
          جلسه
          {{ item.name }}
        </div>
        <span>
          {{ this.utils.monize(item.price) }}
          تومان
        </span>
      </div>

      <div class="col-xl-4 col-lg-5 col-12 text-center text-xl-right">
        <div class="course-card-detail">
          <div class="item text-center">
            <div class="text-center">
              تاریخ برگزاری:
              <span>
                {{ jmoment(item.start_date).format("jYYYY/jMM/jDD") }}
                ,ساعت {{ item.start_time }} الی {{ item.end_time }}
              </span>
            </div>
          </div>
        </div>
      </div>

      <div class="col-xl-3 col-lg-12 col-12 col-lg-2 text-end text-xl-left">
        <router-link
          v-if="item.price == 0"
          :to="{
            name: 'VideoSession',
            params: {
              videoSessionId: item.id,
              productId: productId,
            },
          }"
          class="btn btn-sm btn-success d-block"
        >
          <i class="fas fa-gifts me-1"></i>
          <span>ورود رایگان</span>
        </router-link>

        <router-link
          v-else-if="item.buyed_before"
          class="btn btn-sm btn-success d-block"
          :to="{
            name: 'VideoSession',
            params: {
              videoSessionId: item.id,
              productId: productId,
            },
          }"
        >
          <i class="fas fa-check me-1"></i>
          <span>ورود به کلاس</span>
        </router-link>

        <a
          v-else
          @click="addToCart(item.id)"
          class="btn btn-outline-primary btn-sm d-block"
        >
          <i class="fas fa-shopping-cart me-1"></i>
          <span>افزودن به سبد خرید</span>
          <i
            :id="`progress-${item.id}`"
            class="fas fa-spinner fa-spin d-none"
          ></i>
          <i :id="`check-${item.id}`" class="fas fa-check d-none"></i>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import ArefApi from "@/libs/api.js";
import utils from "@/libs/utils";
import jmoment from "moment-jalaali";

export default {
  name: "VideoSessions",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.jmoment = jmoment;
    this.getProductVideos();
  },
  props: {
    productId: Number,
  },
  emits: {
    changeCart: String,
  },
  components: {},
  data() {
    return {
      videoSessionList: null,
      product: null,
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      cartAlert: null,
    };
  },
  methods: {
    emitChangeCart() {
      this.$emit("changeCart", this.cartAlert);
    },
    getProductVideos() {
      this.arefApi
        .getUserProductVideoSessions(this.productId, 1, true)
        .then((resp) => {
          this.videoSessionList = resp.data.product_detail_videos;
          this.product = resp.data.product;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    addToCart(product_details_id) {
      const obj = {
        products_id: this.productId,
        product_details_id,
      };
      document
        .querySelector(`#progress-${product_details_id}`)
        .classList.remove(`d-none`);
      this.arefApi
        .addMicroProductToCart(obj)
        .then((resp) => {
          document
            .querySelector(`#progress-${product_details_id}`)
            .classList.add(`d-none`);
          document
            .querySelector(`#check-${product_details_id}`)
            .classList.remove(`d-none`);
          this.$store.commit("set", [
            "cartCount",
            this.utils.getCartCount(resp.data),
          ]);

          this.$store.commit("set", ["cartAmount", resp.data.amount]);
          window.scrollTo(0,top);
        })
        .catch((err) => {
          if (err.data.errors.added_before) {
            this.utils.showSuccess(this, "کل محصول قبلا به سبد اضافه شده است");
          } else {
            this.utils.showErrors(this, err);
          }

          document
            .querySelector(`#progress-${product_details_id}`)
            .classList.add(`d-none`);
        });
    },
  },
};
</script>