<template>
  <div class="bg-light p-2">
    <div
      v-if="allChairs"
      class="accordion accordion-flush"
      id="accordionFlushExample"
    >
      <div v-for="item in allChairs" :key="item.id" class="accordion-item">
        <h2 class="accordion-header">
          <button
            class="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            :data-bs-target="'#flush-heading' + item.id"
            aria-expanded="false"
            aria-controls="flush-collapseOne"
          >
            <a class="btn btn-sm btn-success me-2"> کلیک کنید </a>
            ازشماره
            {{ item.start }}
            الی
            {{ item.end }}
            {{ item.description }}
            قیمت:
            {{ item.price.toLocaleString() }}
          </button>
        </h2>
        <div
          :id="'flush-heading' + item.id"
          class="accordion-collapse collapse"
          aria-labelledby="flush-headingOne"
          data-bs-parent="#accordionFlushExample"
        >
          <div class="accordion-body">
            <div class="row">
              <div
                v-for="(n, index) in item.end - item.start + 1"
                :key="index + item.start"
                class="col-3 col-sm-2 col-md-1 form-check mt-2 mt-sm-0"
              >
                <input
                  class="form-check-input"
                  type="checkbox"
                  :value="index + item.start"
                  :id="'flexCheck' + (index + item.start)"
                  :disabled="reservedChairs.indexOf(index + item.start) > -1"
                  @change="addRemoveCard($event)"
                />
                <label
                  class="form-check-label"
                  :for="'flexCheck' + (index + item.start)"
                >
                  {{ index + item.start }}
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <h5 class="text-danger">صندلی های فروخته شده</h5>
      <div>
        <span
          v-for="(item, index) in reservedChairs"
          :key="index"
          class="badge bg-warning text-dark me-1"
        >
          {{ item }}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import utils from "@/libs/utils";
import ArefApi from "@/libs/api.js";

export default {
  props: {
    productId: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      allChairs: null,
      reservedChairs: [],
      selectedChairs: [],
    };
  },
  created() {
    this.utils = new utils();
    this.arefApi = new ArefApi();
    this.getProductChairs();
  },
  methods: {
    getProductChairs() {
      this.arefApi
        .getUserProductChairs(this.productId, 1, true)
        .then((res) => {
          this.allChairs = res.data.chairs;
          const reserved_chairs = res.data.reserved_chairs;
          this.reservedChairs=[];
          for (const item in reserved_chairs){
            this.reservedChairs.push(reserved_chairs[item]);
          }
          //this.reservedChairs = res.data.reserved_chairs;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    addRemoveChair(chairNumber) {
      const index = this.selectedChairs.indexOf(chairNumber);
      if (index > -1) {
        this.selectedChairs.splice(index, 1);
      } else {
        this.selectedChairs.push(chairNumber);
      }
      console.log("this.selectedChairs:", this.selectedChairs);
    },
    async addRemoveCard(event) {
      const chairNumber = event.target.value;
      event.target.disabled = true;
      if (event.target.checked) {
        await this.addToCard(chairNumber);
        this.$swal
          .fire({
            title: "",
            text: "صندلی با موفقیت به سبد خرید اضافه شد",
            icon: "success",
            showCancelButton: true,
            cancelButtonText: "انتخاب صندلی دیگر",
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#198754",
            confirmButtonText: "مشاهده سبد خرید",
          })
          .then((result) => {
            if (result.isConfirmed) {
              this.$router.push({ name: "Cart" });
            }
          });
      } else {
        await this.removeFromCard(chairNumber);
        this.utils.showSuccess(this, "صندلی از سبد خرید حذف شد");
      }
      event.target.disabled = false;
    },
    addToCard(chairNumber) {
      const obj = {
        products_id: this.productId,
        type: "chair",
        chairs: [chairNumber],
      };

      this.arefApi
        .addMicroProductToCart(obj)
        .then((resp) => {
          this.$store.commit("set", [
            "cartCount",
            this.utils.getCartCount(resp.data),
          ]);
          this.$store.commit("set", ["cartAmount", resp.data.amount]);
          return Promise.resolve();
        })
        .catch((err) => {
          if (err.data.errors.added_before) {
            this.utils.showSuccess(this, "کل محصول قبلا به سبد اضافه شده است");
          } else {
            this.utils.showErrors(this, err);
          }
          return Promise.reject();
        });
    },

    removeFromCard(chairNumber) {
      this.arefApi
        .deleteChairNumberFromCard(this.productId, chairNumber)
        .then(() => {
          this.utils.showSuccess(this);
          return Promise.resolve();
        })
        .catch((err) => {
          if (err.data.errors.added_before) {
            this.utils.showSuccess(this, "کل محصول قبلا به سبد اضافه شده است");
          } else {
            this.utils.showErrors(this, err);
          }
          return Promise.reject();
        });
      // const obj = {
      //   products_id: this.productId,
      //   chairs: [chairNumber],
      // };
    },
  },
};
</script>